.calendar {
  display: flex;
  flex-wrap: wrap;

  width: $field-width;
  padding-top: 50px;

  &__day {
    @include day-cell($day-cell-size, $cells-gap);

    position: relative;

    background-color: $cells-bkg-color;
    border: 1px solid #000;

    transition: all 0.5s;

    &:hover {
      cursor: pointer;
      background-color: aquamarine;
      transform: translateY(-30px);
    }

    @for $day-number from 1 through 31 {
      &:nth-child(#{$day-number}) {
        @if $day-number >= 28 and $day-number <=31 {
          display: none;
        }

        background-image: url("https://cataas.com/cat?type=sq#{$day-number}");
        background-size: cover;
        color: white;
      }

      &:nth-child(#{$day-number})::after {
        content: "#{$day-number}";
        font-size: $cells-font-size;
        position: absolute;

        @include before-align-center($day-cell-size);
      }
    }
  }

  @each $day, $margin-left in $start-day {
    &--start-day--#{$day} &__day:first-child {
      margin-left: $margin-left;
    }
  }

  @for $month-length from 28 through 31 {
    &--month-length--#{$month-length} &__day:nth-child(-n + #{$month-length}) {
      display: block;
    }
  }
}
